export const errorHandle = {
  /**
   *
   * @param {Error} e axios
   * @returns String
   */
  errMsg(e) {
    let errStatus = {
      500: "!",
      "Network Error": "",
    };
    let msg = "";
    Object.keys(errStatus).forEach((v) => {
      if (e.message?.includes(v)) {
        msg = errStatus[v];
      }
    });
    return msg;
  },
};
